import _ from "lodash"

export default class ImportedStroke {
    /**
     * @param {object} draw - the svg draw object used to render this stroke
     * @param {object} stroke - the stroke object
     * @param {string} idCreator - the creator ID of the user who created the path
     * @param {number} idStroke - the stroke ID of the path (starts at 1 and increments as each stroke is added)
     * @param {number} status - number indicating the current status of the stroke (1 = 'drawn', 2 = 'erased', 3 = 'moved')
     */
    constructor(draw, stroke, idCreator, idStroke, status) {
        this.type = stroke.type
        this.drawn = false
        this.draw = draw
        this.stroke = stroke
        this.rendered = true
        this.idCreator = idCreator
        this.idStroke = idStroke
        this.status = status
        this.opacity = 1
        this.movedFrom = null
    }

    /**
     * Converts this path into a plain object.
     * @return {object} object containing keys:
     *
     */
    serialize() {
        return {
            msg: 'placeholder text',
            type: this.type,
            status: this.status
        }
    }

    getHTML() {
        return this.stroke.node.outerHTML
    }

    copy() {
        let strokeCopy = _.cloneDeep(this.stroke)
        return new ImportedStroke(this.draw, strokeCopy, this.idCreator, this.idStroke, this.status)
    }

    /**
     * Moves the path by a certain displacement.
     *
     * @param {number} x
     * @param {number} y
     */
    moveBy(x, y) {
        this.stroke.dmove(x, y)
    }

    /**
     * "Highlights" the path by changing its opacity.
     */
    highlight() {
        this.stroke.opacity(this.opacity)
    }

    /**
     * Changes the color of the stroke
     * @param {string} color 
     */
    setColor(color) {
        this.color = color
        console.log(this.stroke.attr('stroke'))
        console.log(this.stroke.attr('fill'))
        if (this.stroke.attr('stroke') !== "#000000" && this.stroke.attr('stroke') !== "none") {
            this.stroke.stroke({color: color})
        }
        if (this.stroke.attr('fill') !== "#000000" && this.stroke.attr('fill') !== "none" ) {
            this.stroke.fill({color: color})
        }
    }

    /**
     * Stop rendering this path on the SVG.
     */
    remove(status) {
        this.stroke.remove()
        this.rendered = false
        this.status = status
        if (status === 2) {
            this.erased = true
        }
    }

    /**
     * Adds this path to the group so it can be rendered.
     *
     * @param {object} sketchGroup
     */
    addToGroup(sketchGroup) {
        sketchGroup.add(this.stroke)
        this.rendered = true
        this.status = 1
    }
}