/** A collection of path. Keeps track of a undo and redo buffer. */
import Path from './path'
import ImportedStroke from './importedstroke'

export default class Sketch {
  /**
   * @param {object} draw - the SVG draw object
   * @param {object} svg - the svg element
   * @param {object} pencilTexture - the pencil texture SVG filter
   */
  constructor(draw, svg, pencilTexture) {
    // this.paths = []
    this.clearedSketches = [[]] // Stores current sketch and all cleared sketches for undoing and redoing clears.
    this.sketchGroup = draw.group()
    this.draw = draw
    this.currentPath = null
    this.undoIndex = 0
    this.clearUndoIndex = 0
    this.svg = svg
    this.prevMouseLocation = null
    this.currMouseLocation = null
    this.beganHighlighting = false
    this.userID = null
    this.currStrokeID = 1
    this.pencilTexture = pencilTexture

    this.originalWidth = this.getWidth()
    this.originalHeight = this.getHeight()
    this.updateDimensions()
    this.animationfinished = true
  }

  getPaths() {
    return this.clearedSketches[this.clearedSketches.length - this.clearUndoIndex - 1]
  }

  setPaths(newPaths) {
    this.clearedSketches[this.clearedSketches.length - this.clearUndoIndex - 1] = newPaths
  }

  updatePaths(paths, newPath) {
    paths.push(newPath)
    this.setPaths(paths)
    this.clearedSketches = this.clearedSketches.slice(0, this.clearedSketches.length - this.clearUndoIndex)
    this.undoIndex = 0
    this.clearUndoIndex = 0
  }

  getWidth() {
    let sketchpad = document.getElementById('sketchpad')
    if (sketchpad !== null) {
      return sketchpad.clientWidth
    }
    return this.svg.clientWidth
  }

  getHeight() {
    let sketchpad = document.getElementById('sketchpad')
    if (sketchpad !== null) {
      return sketchpad.clientHeight
    }
    return this.svg.clientHeight
  }

  addZero(x, n) {
    while (x.toString().length < n) {
      x = "0" + x;
    }
    return x;
  }

  getTime() {
    let d = new Date()
    let h = this.addZero(d.getHours(), 2)
    let m = this.addZero(d.getMinutes(), 2)
    let s = this.addZero(d.getSeconds(), 2)
    let ms = this.addZero(d.getMilliseconds(), 3)
    return h + ":" + m + ":" + s + ":" + ms
  }

  /**
   * Serialize a path into an array of objects
   * @return {Array}
   */
  serialize() {
    let serialized = []
    let paths = this.getPaths()
    for (let i = 0; i < paths.length - this.undoIndex; i++) {
      serialized.push(paths[i].serialize())
    }
    return serialized
  }

  /**
   * Loads in a serialized sketch.
   * @param {Object} serializedSketch
   */
  loadSketch(serializedSketch) {
    this.remove()
    this.setPaths([])
    for (let serializedPath of serializedSketch) {
      if (serializedPath.status === 1) {
        let path = Path.deserialize(serializedPath, this.draw, this.pencilTexture)
        if (path !== null) {
          path.hasprettyStroke = false
          if (path.status !== 1) path.rendered = false
          this.getPaths().push(path)
        }
      }
    }
  }

  displayLoadedSketch(scaleSketch) {
    scaleSketch = scaleSketch === undefined ? true : scaleSketch
    this.sketchGroup.remove()
    this.sketchGroup = this.draw.group()
    if (!scaleSketch) {
      this.updateDimensions()
    }

    for (let path of this.getPaths()) {
      path.addToGroupSmoothed(this.sketchGroup)
      if (path.status !== 1) path.remove(path.status)
      if (path.created === "move") {
        path.movedFrom = this.getPaths().find(el => el.idStroke === path.idMovedFrom)
      }
    }
    let width = this.getWidth()
    let height = this.getHeight()

    if (!scaleSketch) {
      return
    }

    // scale snooped sketch to fit.
    let bbox = this.sketchGroup.bbox()

    let snoopHeight = height
    let votebar = document.getElementById('votebar')
    if (votebar !== null) {
      snoopHeight = snoopHeight - votebar.getClientRects()[0].height
    }
    let scale = Math.min(width / bbox.width, snoopHeight / bbox.height)

    this.sketchGroup.transform({ // Center peeked sketch
      x: width / 2 - bbox.cx,
      y: snoopHeight / 2 - bbox.cy,
      relative: true,
    })
    this.sketchGroup.transform({
      scaleX: scale * 0.8, // Include some padding
      scaleY: scale * 0.8,
    })
  }

  /**
   * Used on the adminpage to indicate which sketches are blank or peek-only
   */
  displayText(sketchType) {
    var text
    if (sketchType === "peek") {
      text = this.draw.text("User has only peeked so far.")
      text.move(20, 70)
    }
    else {
      text = this.draw.text("User has not sketched \nnor peeked so far.")
      text.move(40, 70)
    }
    this.sketchGroup = text
  }

  /**
   * Reset the origin to the center of image and scale sketch to fit screen, assuming it fit in original width and
   * height.
   */
  updateDimensions() {
    let xScaleFactor = (this.getWidth() / this.originalWidth)
    let yScaleFactor = (this.getHeight() / this.originalHeight)

    this.updateOrigin()
    let scaleFactor = Math.min(xScaleFactor, yScaleFactor)

    this.sketchGroup.transform({
      scale: scaleFactor,
      cx: 0,
      cy: 0,
    })
  }

  updateOrigin() {
    this.sketchGroup.transform({
      scale: 1,
    })
    this.sketchGroup.transform({
      x: this.getWidth() / 2,
      y: this.getHeight() / 2,
    })
  }

  /**
   * Undo an operation (erase, move, draw, clear)
   */
  undo(targetPath) {
    if (this.undoIndex < this.getPaths().length) {
      // let targetPath = this.getPaths()[this.getPaths().length - this.undoIndex - 1]
      if (!targetPath) return false;
      if (targetPath.status === 2 && !targetPath.rendered && targetPath.erased) { // undo erase
        targetPath.addToGroup(this.sketchGroup)
      }
      else if (targetPath.rendered && targetPath.movedFrom !== null && !targetPath.movedFrom.rendered) { //undo move
        targetPath.remove(3)
        targetPath.movedFrom.addToGroup(this.sketchGroup)
      }
      else { //undo draw
        targetPath.remove(2)
      }
      this.undoIndex += 1
    } else if (this.clearUndoIndex < this.clearedSketches.length - 1) { // Undoing a clear
      this.undoIndex = 0
      this.clearUndoIndex += 1
      for (let path of this.getPaths()) {
        if (path.rendered) {
          path.addToGroup(this.sketchGroup)
        }
      }
    } else {
      return false
    }
    return true
  }

  /**
   * Redo an operation (erase, move, draw, clear)
   */
  redo(targetPath) {
    if (this.undoIndex > 0) {
      // let targetPath = this.getPaths()[this.getPaths().length - this.undoIndex]
      if (!targetPath) return false;
      if (targetPath.rendered && targetPath.erased) { // redo erase
        targetPath.remove()
      }
      else if (!targetPath.rendered && targetPath.movedFrom !== null && targetPath.movedFrom.rendered) { //redo move
        targetPath.addToGroup(this.sketchGroup)
        targetPath.movedFrom.remove(3)
      }
      else { //redo draw
        targetPath.addToGroup(this.sketchGroup)
      }
      this.undoIndex -= 1
    } else if (this.clearUndoIndex > 0) { //redo clear
      this.remove()
      this.clearUndoIndex -= 1
      this.undoIndex = this.getPaths().length
    } else {
      return false
    }
    return true
  }

  /**
   * Clears all strokes
   */
  clear() {
    if (this.getPaths().length === 0) {
      return false
    }
    this.setPaths(this.getPaths().slice(0, this.getPaths().length - this.undoIndex))
    this.clearedSketches = this.clearedSketches.slice(0, this.clearedSketches.length - this.clearUndoIndex)
    this.clearUndoIndex = 0
    this.undoIndex = 0
    this.remove()
    this.clearedSketches.push([])
    return true
  }

  /**
   * Visually removes the sketch group
   */
  remove() {
    this.sketchGroup.remove()
    this.sketchGroup = this.draw.group()
    this.updateDimensions()
  }

  /**
   * Erases a stroke by making a copy of the stroke and hiding both
   */
  erase(mouseX, mouseY) {
    let selected = this.select(mouseX, mouseY)
    if (typeof selected !== 'undefined') {
      let paths = selected[0]
      let targetPath = selected[1]
      targetPath.remove(2)
      this.updatePaths(paths, targetPath)
      //this.ecThis=targetPath
      return true
    }
    return false
  }


  color(mouseX, mouseY, color) {
    let selected = this.select(mouseX, mouseY)
    if (typeof selected !== 'undefined') {
      let targetPath = selected[1]
      targetPath.setColor(color)
      //this.ecThis=targetPath
      return true
    }
    return false
  }

  pickColor(mouseX, mouseY) {
    let selected = this.select(mouseX, mouseY)
    if (typeof selected !== 'undefined') {
      let targetPath = selected[1]
      return targetPath.color
    }
    else {
      return false
    }
  }

  /**
   * Selects the stroke closest to mouse position
   */
  select(clientX, clientY) {
    if (this.getPaths().length > 0) {
      let rect = this.svg.getBoundingClientRect()
      let transform = this.sketchGroup.transform()
      let mouseX = ((clientX - rect.left) - transform.x) / transform.scaleX
      let mouseY = ((clientY - rect.top) - transform.y) / transform.scaleY
      this.prevMouseLocation = [mouseX, mouseY]
      this.currMouseLocation = [mouseX, mouseY]
      if (!Number.isNaN(mouseX) && !Number.isNaN(mouseY)) {
        let buffer = 40
        let paths = this.getPaths().slice(0, this.getPaths().length - this.undoIndex)
        let closest_distances = []
        for (let i = 0; i < paths.length; i++) {
          if (paths[i].status === 1) {
            if (paths[i].drawn) {
              closest_distances.push(this.getClosestDistanceDrawnSVG(paths[i].pathCoords, mouseX, mouseY))
            }
            else {
              closest_distances.push(this.getClosestDistanceImportedSVG(
                paths[i].stroke.node.getBoundingClientRect(), clientX, clientY)
              )
            }
          }
          else {
            closest_distances.push(9999)
          }
        }
        let min_distance = Math.min.apply(null, closest_distances)
        if (min_distance < buffer) {
          return [paths, paths[closest_distances.indexOf(min_distance)]]
        }
      }
    }
  }

  /**
   * Given a list of stroke coords, find the minimum distance between any point in the list and mouse position
   */
  getClosestDistanceDrawnSVG(coords, mouseX, mouseY) {
    var distances = []
    for (let i = 0; i < coords.length; i += 2) {
      let x = coords[i] - mouseX
      let y = coords[i + 1] - mouseY
      distances.push(Math.sqrt(x * x + y * y))
    }
    return Math.min.apply(null, distances)
  }

  getClosestDistanceImportedSVG(boundingRect, mouseX, mouseY) {
    var distances = []
    for (let width = 0; width < boundingRect.width; width++) {
      for (let height = 0; height < boundingRect.height; height++) {
        let x = boundingRect.x + width - mouseX
        let y = boundingRect.y + height - mouseY
        distances.push(Math.sqrt(x * x + y * y))
      }
    }
    return Math.min.apply(null, distances)
  }

  /**
   * Starts moving the stroke closest to mouse position by 
   * - Making a copy of the stroke
   * - Hiding the original stroke
   * - Setting the copied stroke as the one targetted for moving
   */
  startMove(event) {
    if (event.type.startsWith('touch')) {
      event = event.changedTouches[0]
    }
    let selected = this.select(event.clientX, event.clientY)
    if (typeof selected !== 'undefined') {
      // makes (unrendered) copy of target path for future undo and adds to stack 
      let paths = selected[0]
      let targetPath = selected[1]
      let newTargetPath
      if (targetPath.drawn) {
        newTargetPath = Path.deserialize(targetPath.serialize(), this.draw, this.pencilTexture)
        newTargetPath.addToGroupSmoothed(this.sketchGroup) // necessary, otherwise copied path off position
      }
      else {
        let svgHolder = this.draw.svg(targetPath.getHTML())
        newTargetPath = new ImportedStroke(this.draw, svgHolder.children()[3], this.userID, this.currStrokeID, 1)
        newTargetPath.addToGroup(this.sketchGroup)
      }
      newTargetPath.timeStart = this.getTime()
      newTargetPath.idStroke = this.currStrokeID
      newTargetPath.pencilTextureToggle = targetPath.pencilTextureToggle
      newTargetPath.created = 2
      newTargetPath.idMovedFrom = targetPath.idStroke
      newTargetPath.movedFrom = targetPath
      targetPath.remove(3)
      this.currStrokeID += 1
      newTargetPath.opacity = 0.1
      newTargetPath.highlight()
      this.updatePaths(paths, newTargetPath)
      this.currentPath = newTargetPath
      return true
    }
    return false
  }

  endMove() {
    this.currentPath.opacity = 1
    this.currentPath.highlight()
    this.currentPath.timeEnd = this.getTime()
    let saveData = this.prevMouseLocation.concat(this.currMouseLocation)
    this.currMouseLocation = null
    return saveData
  }

  hide() {
    this.sketchGroup.hide()
  }

  show() {
    this.sketchGroup.show()
  }

  /**
   * Depending on mode, adds a point onto the current or moves path
   * @param {object} event
   * @param {string} mode
   */
  continueLineWithEvent(event, mode, webcamX, webcamY) {
    let rect = this.svg.getBoundingClientRect()
    let x
    let y
    let transform = this.sketchGroup.transform()
    // Transform coordinates on svg div to center origin coordinates of sketchGroup
    if (event === null) {
      x = ((webcamX - rect.left) - transform.x) / transform.scaleX
      y = ((webcamY - rect.top) - transform.y) / transform.scaleY
    }
    else {
      if (event.type.startsWith('touch')) {
        event = event.changedTouches[0]
      }
      x = ((event.clientX - rect.left) - transform.x) / transform.scaleX
      y = ((event.clientY - rect.top) - transform.y) / transform.scaleY
    }

    if (mode === 'draw') {
      this.currentPath.addPoint(x, y)
      // this.sketchEdit('point')
    }
    else if (mode === 'move') {
      if (this.currMouseLocation !== null) {
        this.currentPath.moveBy(x - this.currMouseLocation[0], y - this.currMouseLocation[1])
        this.currMouseLocation = [x, y]
      }
    }
  }

  /**
   *
   * @param {string} color
   * @param {number} width
   * @param {boolean} pencilTextureToggle
   */
  startPath(color, width, pencilTextureToggle) {
    this.currentPath = new Path(color, width, [], this.draw, this.userID, this.currStrokeID, 1, 0, 1, "", "", pencilTextureToggle, this.pencilTexture)
    this.currStrokeID += 1
    this.currentPath.addToGroup(this.sketchGroup)
    this.currentPath.timeStart = this.getTime()
  }

  /**
   * Adds the current path to the list of other paths.
   */
  finishPath() {
    this.currentPath.timeEnd = this.getTime()
    let paths = this.getPaths().slice(0, this.getPaths().length - this.undoIndex)
    this.updatePaths(paths, this.currentPath)
    this.currentPath.addToGroupSmoothed(this.sketchGroup)
    //this.currentPath = null
  }

  /**
   * Add new path based on JSON specifications
   * @param {*} json 
   */
  addPathFromCode(json) {
    let newTargetPath = Path.deserialize(json, this.draw, this.pencilTexture)
    let paths = this.getPaths().slice(0, this.getPaths().length - this.undoIndex)
    let targetPath = this.currentPath
    newTargetPath.idStroke = this.currStrokeID
    newTargetPath.pencilTextureToggle = targetPath.pencilTextureToggle
    newTargetPath.created = 2
    this.currStrokeID += 1
    newTargetPath.idMovedFrom = targetPath.idStroke
    newTargetPath.movedFrom = targetPath
    newTargetPath.hasprettyStroke = false
    targetPath.remove(3)
    newTargetPath.addToGroupSmoothed(this.sketchGroup) // necessary, otherwise copied path off position
    newTargetPath.timeStart = this.getTime()
    this.updatePaths(paths, newTargetPath)
    this.currentPath = newTargetPath
  }

  /**
   * Highlight the stroke closest to mouse position
   */
  highlightPeekedStroke(event) {
    let selected = this.select(event.clientX, event.clientY)
    if (typeof selected !== 'undefined') {
      selected[1].highlight()
      this.beganHighlighting = true
    }
  }

  /**
   * Returns all highlighted strokes
   */
  getHighlightedPeekedStrokes() {
    let strokeList = []
    for (let path of this.getPaths()) {
      if (path.peekHighlighted) {
        strokeList.push(path)
      }
    }
    return strokeList
  }

  /**
   * Create a pseudo animation for drawing by gradually creating a path with same coordinates as most recent path, then deleting that path
   * @param {string} interaction
   */
  animateNewPeekedStroke(interaction) {
    if (interaction !== 'sketch') {
      return
    }
    let paths = this.getPaths()
    let path = paths[paths.length - 1]
    if (path.created === 1 && path.status === 1) {
      if (!this.animationfinished) {
        this.currentPath.remove(2)
        path.addToGroup(this.sketchGroup)
        this.animationfinished = true
        clearInterval(this.intvl)
      }
      this.animationfinished = false
      path.remove(2)
      let totalTime = this.convertToMillisec(path.timeEnd) - this.convertToMillisec(path.timeStart)
      let intvlTime = totalTime / (path.pathCoords.length / 2)
      let i = 0
      this.startPath(path.color, path.width, path.pencilTextureToggle, this.pencilTexture)
      this.intvl = setInterval(() => {
        if (i >= path.pathCoords.length) {
          this.currentPath.addToGroupSmoothed(this.sketchGroup)
          this.currentPath.remove(2)
          path.addToGroup(this.sketchGroup)
          this.animationfinished = true
          clearInterval(this.intvl)
        }
        else {
          this.currentPath.addPoint(path.pathCoords[i], path.pathCoords[i + 1])
          i += 2
        }
      }, intvlTime)
    }
  }

  /**
   * Converts time with format (hh:mm:ss:ms) into milliseconds
   * @param {string} time 
   */
  convertToMillisec(time) {
    let l = time.split(":")
    return (parseInt(l[0] * 3600000) + parseInt(l[1] * 60000) + parseInt(l[2] * 1000) + parseInt(l[3]))
  }

  addImportedStroke(stroke) {
    this.currentPath = new ImportedStroke(this.draw, stroke, this.userID, this.currStrokeID, 1)
    this.currStrokeID += 1
    this.currentPath.timeEnd = this.getTime()
    let paths = this.getPaths().slice(0, this.getPaths().length - this.undoIndex)
    this.updatePaths(paths, this.currentPath)
    this.currentPath.addToGroup(this.sketchGroup)
  }
}