import _ from "lodash"
import 'svg.filter.js'

/** Single stroke of a sketch. SVG implementation. */
export default class Path {
  /**
   * @param {string} color - the color of the path
   * @param {number} width - the width of the path
   * @param {object} initialCoords - the sketch that this path is contained in
   * @param {object} draw - the svg draw object used to render this path
   * @param {string} idCreator - the creator ID of the user who created the path
   * @param {number} idStroke - the stroke ID of the path (starts at 1 and increments as each stroke is added)
   * @param {number} status - number indicating the current status of the stroke (1 = 'drawn', 2 = 'erased', 3 = 'moved')
   * @param {number} idMovedFrom - the id of the stroke this stroke was moved from (not moved = 0)
   * @param {number} created - number indicating how the stroke was created (1 = 'drawn', 2 = 'moved', 3 = 'copied')
   * @param {string} timeStart - time when interaction with this stroke started (hh:mm:ss:ms)
   * @param {string} timeEnd - time when interaction with this stroke ended (hh:mm:ss:ms)
   * @param {boolean} pencilTextureToggle - boolean to indicate whether the stroke has a pencil texture
   * @param {object} pencilTexture - the pencil texture SVG filter
   * pencil texture modified from https://heredragonsabound.blogspot.com/2020/02/creating-pencil-effect-in-svg.html
   */
  constructor(color, width, initialCoords, draw, idCreator, idStroke, status, idMovedFrom,
    created, timeStart, timeEnd, undone, pencilTextureToggle, pencilTexture) {
    this.pathCoords = initialCoords  // Flattened array of points, stored as [x1, y1, x2, y2, ...]
    this.svgPath = null

    this.color = color
    this.width = width
    this.opacity = 1
    this.draw = draw
    this.options = { width: this.width, color: this.color, opacity: this.opacity, linecap: 'round', linejoin: 'round'}
    this.rendered = true
    this.erased = false
    this.idCreator = idCreator
    this.idStroke = idStroke
    this.status = status 
    this.idMovedFrom = idMovedFrom 
    this.movedFrom = null
    this.created = created 
    this.timeStart = timeStart
    this.timeEnd = timeEnd
    this.pencilTextureToggle = pencilTextureToggle
    this.pencilTexture = pencilTexture
    this.drawn = true
    this.undone = undone

    this.svgPath = this.draw.polyline(this.pathCoords).fill('none')
        .stroke(this.options)
    if (this.pencilTextureToggle) { // if stroke is on paper texture, adopt a pencil texture
      this.svgPath.filter(this.pencilTexture)
    }
  }

  /**
   * Converts this path into a plain object.
   * @return {object} object containing keys:
   *
   */
  serialize() {
    let coordscopy = _.cloneDeep(this.pathCoords)
    return {
      coords: coordscopy,
      color: this.color,
      width: this.width,
      idCreator: this.idCreator,
      idStroke: this.idStroke,
      status: this.status,
      idMovedFrom: this.idMovedFrom,
      created: this.created,
      timeStart: this.timeStart,
      timeEnd: this.timeEnd,
      undone: this.undone
      // pencilTextureToggle: this.pencilTextureToggle
    }
  }

  /**
   *
   * @param {object} serializedPath
   * @param {object} draw
   * @return {Path} the deserialized path
   */
  static deserialize(serializedPath, draw, pencilTexture) {
    return new Path(serializedPath.color, serializedPath.width, serializedPath.coords, draw, 
      serializedPath.idCreator, serializedPath.idStroke, serializedPath.status, serializedPath.idMovedFrom,
      serializedPath.created, serializedPath.timeStart, serializedPath.timeEnd, serializedPath.undone, serializedPath.pencilTextureToggle, pencilTexture)
  }

  /**
   * Adds a point to the path and renders it.
   *
   * @param {number} x
   * @param {number} y
   */
  addPoint(x, y) {
    this.pathCoords.push(x)
    this.pathCoords.push(y)
    this.svgPath.plot(this.pathCoords)
  }
  /**
   * Moves the path by a certain displacement.
   *
   * @param {number} x
   * @param {number} y
   */
  moveBy(x, y) {
    for (let i = 0; i < this.pathCoords.length; i++) {
      this.pathCoords[i] = (i % 2 === 0) ? this.pathCoords[i] + x : this.pathCoords[i] + y
    }
    this.svgPath.dmove(x, y)
  }

  /**
   * "Highlights" the path by changing its opacity.
   */
  highlight() {
    this.svgPath.opacity(this.opacity)
  }

  /**
   * Changes the color of the path
   * @param {string} color 
   */
  setColor(color) {
    this.color = color
    this.svgPath.stroke({color: color})
  }

  /**
   * Stop rendering this path on the SVG.
   */
  remove(status) {
    this.svgPath.remove()
    this.rendered = false
    this.status = status
    if (status === 2) {
      this.erased = true
    }
  }

  /**
   * Adds this path to the group so it can be rendered.
   *
   * @param {object} sketchGroup
   */
  addToGroup(sketchGroup) {
    sketchGroup.add(this.svgPath)
    this.rendered = true
    this.status = 1
  }

  pathCoordsAtIndex(coords, index, xy) {
    return coords[index * 2 + xy]
  }

  smoothCoords(coords) {
    let str = ''
    str += ('M ' + this.pathCoordsAtIndex(coords, 0, 0) + ' ' + this.pathCoordsAtIndex(coords, 0, 1) + ' ')
    let skip1 = true
    let skip2 = false
    let cp1x, cp1y, cp2x, cp2y
    for (let i = 0; i < coords.length / 2 - 1; i++) {
      if (skip1) {
        cp1x = this.pathCoordsAtIndex(coords, i, 0) // x
        cp1y = this.pathCoordsAtIndex(coords, i, 1) // y
        skip1 = false
        skip2 = true
      }
      if (skip2) {
        cp2x = this.pathCoordsAtIndex(coords, i, 0) // x
        cp2y = this.pathCoordsAtIndex(coords, i, 1) // y

        skip1 = false
        skip2 = false
      } else {
        str += 'C ' + cp1x + ' ' + cp1y + ' ' + cp2x + ' ' + cp2y + ' ' +
          this.pathCoordsAtIndex(coords, i, 0) + ' ' + this.pathCoordsAtIndex(coords, i, 1) + ' '
        skip1 = true
        skip2 = false
      }
    }
    return str
  }

  addToGroupSmoothed(sketchGroup) {
    this.svgPath.remove()
    let path = this.draw.path(this.smoothCoords(this.pathCoords))
        .fill('none')
        .stroke(this.options)
    if (this.pencilTextureToggle) {
      path.filter(this.pencilTexture)
    }
    this.svgPath = path
    sketchGroup.add(path)
  }
}
