import React, { Component } from 'react'
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import CreateIcon from '@material-ui/icons/Create';
import PaletteIcon from '@material-ui/icons/Palette';
import DeleteIcon from '@material-ui/icons/Delete';
import PanToolIcon from '@material-ui/icons/PanTool';
import UndoIcon from '@material-ui/icons/Undo';
import RedoIcon from '@material-ui/icons/Redo';
import DeleteSweepIcon from '@material-ui/icons/Clear';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import { ColorPicker } from './colorpicker';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import ColorizeIcon from '@material-ui/icons/Colorize';

import './topbar.css'

export class Topbar extends Component {
    constructor(props) {
        super(props)
        this.state = {
            mode: "draw"
        }
    }

    changeMode = (_, newMode) => {
        this.setState({ mode: newMode })
    }

    handleStrokeSizeChange = (event) => {
        this.props.changeStrokeSize(event.target.value)
    }

    render() {
        return (
            <div id="topbar">
                <ColorPicker color={this.props.color} changeColor={this.props.changeColor}/>
                <ToggleButtonGroup className="float-btn" value={this.state.mode} exclusive onChange={this.changeMode} aria-label="sketch mode">
                    <ToggleButton onClick={() => this.props.drawMode()} value="draw" aria-label="draw mode">
                        <Tooltip title="Draw Mode">
                            <CreateIcon />
                        </Tooltip>
                    </ToggleButton>
                    <ToggleButton onClick={() => this.props.colorMode()} value="color" aria-label="color mode">
                        <Tooltip title="Change Stroke Color">
                            <PaletteIcon />
                        </Tooltip>
                    </ToggleButton>
                    <ToggleButton onClick={() => this.props.pickColor()} value="pickColor" aria-label="pick color">
                        <Tooltip title="Pick Color">
                            <ColorizeIcon />
                        </Tooltip>
                    </ToggleButton>
                    <ToggleButton onClick={() => this.props.eraseMode()} value="erase" aria-label="erase mode">
                        <Tooltip title="Erase Stroke">
                            <DeleteIcon />
                        </Tooltip>
                    </ToggleButton>
                    <ToggleButton onClick={() => this.props.moveMode()} value="move" aria-label="move mode">
                        <Tooltip title="Move Stroke">
                            <PanToolIcon style={{ height: 20 }} /></Tooltip>
                    </ToggleButton>
                </ToggleButtonGroup>
                <Tooltip title="Undo">
                    <IconButton onClick={() => this.props.undo()} color="secondary" aria-label="undo stroke">
                        <UndoIcon />
                    </IconButton>
                </Tooltip>
                <Tooltip title="Redo">
                    <IconButton onClick={() => this.props.redo()} color="secondary" aria-label="redo stroke">
                        <RedoIcon />
                    </IconButton>
                </Tooltip>
                <Tooltip title="Clear Canvas">
                    <IconButton onClick={() => this.props.clear()} color="secondary" aria-label="clear drawing">
                        <DeleteSweepIcon />
                    </IconButton>
                </Tooltip>
                <FormControl>
                    <InputLabel color="secondary">Size</InputLabel>
                    <Select
                        color="secondary"
                        value={this.props.strokeSize}
                        onChange={this.handleStrokeSizeChange}>
                            {[...Array(60).keys()].map(
                                (num) => 
                                <MenuItem key={"stroke-size-"+num} value={num}>{num}</MenuItem>
                            )}
                    </Select>
                </FormControl>
            </div>
        )
    }
}